import { Component, OnInit, Input, OnDestroy, HostListener } from '@angular/core';
import { BaseView } from '../cee-base-class';
import { UserDataHandlerService } from '../../services/user-data-handler.service';
import { WfeStepLoaderService } from '../../services/wfe-step-loader.service';
import { SharedEventsServiceService } from '../../services/shared-events-service.service';

import { AppDataService } from '../../models/app-data/app-data.service';
import { ApiDataService } from '../../models/api-data/api-data.service';
import { MatDialog as MatDialog } from '@angular/material/dialog';
import { MatSnackBar as MatSnackBar } from '@angular/material/snack-bar';
import { FlatUnflat } from '../../utils/flat-unflat-json';
import { HttpClient } from '@angular/common/http';
import { CEEInternalEmitterService } from '../../services/internal-cee-emitter-service.service';
import { MatTooltipModule } from '@angular/material/tooltip';
import { NgIf, NgFor } from '@angular/common';
import { TooltipModule } from '../../directives/tooltip/tooltip.module';

@Component({
    selector: 'app-cee-image',
    templateUrl: './cee-image.component.html',
    styleUrls: ['./cee-image.component.scss', '../field-common.scss'],
    standalone: true,
    imports: [MatTooltipModule, NgIf, NgFor, TooltipModule]
})
export class CeeImageComponent extends BaseView implements OnInit, OnDestroy {

    @Input() declare stepId: any;
    @Input() declare fieldData: any;
    @Input() primaryKeyIndex: any;
    @Input() keepState = false;
    @Input() rowData: any;

    images = [];
    private flatUnflat: FlatUnflat;
    constructor(
        public userDataHandlerService: UserDataHandlerService,
        public sharedEventsService: SharedEventsServiceService,
        public internalCEEEmitter: CEEInternalEmitterService,
        public appDataService: AppDataService,
        public apiDataService: ApiDataService,
        public wfeStepLoaderService: WfeStepLoaderService,
        public dialog: MatDialog,
        public snackBar: MatSnackBar,
        public http: HttpClient
    ) {
        super(userDataHandlerService,
            sharedEventsService,
            internalCEEEmitter,
            appDataService, apiDataService, wfeStepLoaderService, dialog, snackBar, http);
        this.flatUnflat = new FlatUnflat(appDataService);
    }

    ngOnInit() {
        this.init(this.stepId, this.fieldData, this.keepState, this.rowData);
    }

    //@HostListener('window:beforeunload')
    onDestroy() {
        this.onViewUnload();
    }

    ngOnDestroy() {
        this.onViewUnload();
    }

    concatData() { }

    onViewDataInit(): void {
        if (this.fieldData.field_style.field_assets !== undefined) {
            if (this.fieldData.field_style.field_assets.length > 0) {
                for (const fieldAsset of this.fieldData.field_style.field_assets) {
                    this.images.push(fieldAsset.path);
                }
            }
        }
        if (this.rowData) {
            this.images = [];
            if (this.rowData.hasOwnProperty('value')) {
                if (this.fieldData.possible_values.length > 0) {
                    for (const val of this.fieldData.possible_values) {
                        if (this.commonUtil.getPossibleId(val) == this.rowData.value) {
                            this.images.push(this.commonUtil.getPossibleValue(val));
                        }
                    }
                } else {
                    this.images.push(this.rowData.value);
                }
            }
        }
    }
    onSessionDataUpdated(apiKey: string, value: any): void {
        // throw new Error('Method not implemented.');
    }
    onAPICallback(data: any): void {
        data = this.flatUnflat.createNestedObject(data);
        if (localStorage.getItem('singleApiKey') === 'true') {
            this.images = [...this.images, ...this.returnImageData(this.fieldData.api_key, data)];
        } else {
            if (this.fieldData.response_api_key.includes('|')) {
                let multipleImages = [];
                for (const singleData of this.fieldData.response_api_key.split('||').map(str => str.trim())) {
                    multipleImages = [...multipleImages, ...this.returnImageData(singleData, data)];
                }
                this.images = [...this.images, ...multipleImages];
            } else {
                this.images = [...this.images, ...this.returnImageData(this.fieldData.response_api_key, data)];
            }
        }
    }
    onSetSessionData(apiKey: string, value: any): void {
        // throw new Error('Method not implemented.');
    }

    returnImageData(apiKey, data) {
        const imageData = [];
        if (apiKey !== '') {
            if (apiKey.indexOf('[*]') >= 0) {
                const key = apiKey.split('[*]');
                const possibleIds = (this.checkForArray(this.checkForObject(data, key[0]), key[1]));
                for (const ids of possibleIds) {
                    if (this.fieldData.possible_values.length > 0) {
                        for (const possibleValues of this.fieldData.possible_values) {
                            if (this.commonUtil.getPossibleId(possibleValues) == ids) {
                                imageData.push(this.commonUtil.getPossibleValue(possibleValues));
                            }
                        }
                    } else {
                        imageData.push(ids);
                    }
                }
            } else if (data[apiKey]) {
                if (this.fieldData.possible_values.length > 0) {
                    for (const val of this.fieldData.possible_values) {
                        if (this.commonUtil.getPossibleId(val) == data[apiKey]) {
                            imageData.push(this.commonUtil.getPossibleValue(val));
                        }
                    }
                } else {
                    imageData.push(data[apiKey]);
                }
            }
        }

        return imageData;
    }

    onEmptySession(getData: any) {
        // throw new Error('Method not implemented.');
    }
    handleCustomFunction(getData: any) {
        // throw new Error('Method not implemented.');
    }

    setFieldValueOnSetValuesEvent(data: object) {

    }

    setFieldStateOnStateChangeEvent(data: object) {

    }

    updateStateHistory(data: any): void {
        // throw new Error("Method not implemented.");
    }

    toggleLanguage(data: object) {

    }
}
